.header__bar{position:absolute;width:100%;height:40px;left:0px;top:0px;background:#6320ee;display:flex;flex-direction:row;justify-content:space-between;margin-bottom:32px}.bank-name{margin:8px 0 8px 0;width:127px;height:21px;font-family:"Roboto",sans-serif;font-style:normal;font-weight:500;font-size:18px;line-height:21px;color:#e7f0ff;text-shadow:0px 4px 4px rgba(4,0,0,.7)}.button-login{width:94px;height:28px;margin:6px 10px 0 8px;background:#4392f1;border:1px solid rgba(0,0,0,.2);box-shadow:0px 4px 4px rgba(0,0,0,.25);border-radius:4px;font-family:"Roboto",sans-serif;font-style:normal;font-weight:normal;font-size:12px;line-height:14px;color:#e7f0ff}a{color:#e7f0ff}@media(min-width: 721px){.button-login{height:34px;width:120px;margin:12px 16px 0 8px}}.hamburger,.navigation{transition:transform .3s .1s ease-in-out}.hamburger--active{transform:translatex(248px);z-index:300}.hamburger__box{width:35px;height:24px;display:inline-block;position:relative}.hamburger__inner{width:100%;height:3px;background-color:#fefefe;position:absolute;left:0;top:50%;transform:translateY(-50%);transition:background-color .1s .2s ease-in-out}.hamburger__inner::before,.hamburger__inner::after{width:100%;height:3px;background-color:#fefefe;position:absolute;content:"";left:0;transition:transform .2s .2s ease-in-out}.hamburger__inner::before{top:-10px}.hamburger__inner::after{top:10px}.hamburger--active .hamburger__inner{background-color:transparent}.hamburger--active .hamburger__inner:before{transform:translateY(10px) rotate(45deg)}.hamburger--active .hamburger__inner:after{transform:translateY(-10px) rotate(-45deg)}.hamburger{padding:10px;display:inline-block;cursor:pointer;background-color:#6320ee;border:0;margin:0}.navigation{height:calc(100vh + 30px);width:300px;background:#6320ee;background:linear-gradient(0deg, rgba(99, 32, 238, 0) 27%, rgba(99, 32, 238, 0.686825054) 50%, #6320ee 73%);position:absolute;top:-30px;left:-300px;margin-top:0}.navigation--active{transform:translateX(300px);z-