mg buttons"}.presentation__account--picture{grid-area:img;width:260px;height:300px}.presentation__account--text-container{grid-area:txt;max-width:75%}.presentation__account----button-container{grid-area:buttons}}.footer{margin-top:40px;background:#e0e0e0;display:flex;padding:0 40px;justify-content:space-between;border-top:1px solid #bfbfbf;color:#4392f1;text-shadow:2px 2px 2px rgba(49,49,49,.3)}.footer__navigation{padding-left:0}.social__list{margin-bottom:0}@media(min-width: 601px){.footer{display:flex;flex-direction:column}.footer__list{display:flex;flex-direction:row;justify-content:center}.footer__list--element{margin:4px 16px}.social__list{display:flex;flex-direction:row;justify-content:center}.social__list--link{margin:4px 16px;padding-bottom:24px}}.news__container{margin:8px;background:#e7f0ff;box-shadow:0px 4px 4px rgba(0,0,0,.25);border-radius:4px;display:flex;flex-direction:column;align-items:center}.news__big-header{margin:68px 0 50px 15vw}.news__header{margin:40px 0 30px 0;text-align:center}.button-news{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;background:#e7f0ff;border:1px solid rgba(0,0,0,.2);box-shadow:0px 4px 4px rgba(0,0,0,.25);border-radius:4px;color:#4392f1;padding:4px 15px;margin:16px;text-decoration:none;font-weight:bold}@media(min-width: 481px){.news{max-width:536px;margin:0 auto}.news__container{padding:8px 16px;margin:16px}}@media(min-width: 721px){.hamburger{display:none}.navigation{position:relative;margin-top:0;top:10px;left:0;width:100%;height:70px;background:#e5e5e5}.navigation__list{display:flex;flex-direction:row;justify-content:space-around;padding:10px 30px}.header__bar{height:100px}.bank-name{font-size:24px;margin-left:40px}a{color:#6320ee}}h3,p{margin:8px}button{background:#6320ee;font-size:15px}.button-knowledge{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;background:#e7f0ff;border:1px solid rgba(0,0,0,.2);box-shadow:0px 4px 4px rgba(0,0,0,.25);border-radius:4px;color:#4392f1;padding: